/* Google Poppins font*/
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/*Global variable*/
:root{
  /*bg color*/
  --primary-color:#990033;
  --secondart-color: #ffc2b4;
  --primary-nav-color:#e21056;
  --primary-bg:rgb(153 0 51 / 14%);
  --font-clor: #b7b7b7;
  --bg-color: #171717;
  --bg-dark-color: #0c0b0b;
  --white-color: #fdfffc;
  --green-color:#0abf53;
  --green-color-bg: rgb(10 191 83 / 17%);
  --blue-color: #6168ff;
  --blue-color-bg: rgb(97 104 255 / 9%);
  --border-color-light: rgb(255,255,255,0.20);
  --box-shadow: 0 1rem 3rem rgb(0 0 0 / 12%);
  --dark-layer-color: linear-gradient(to top, rgba(79, 52, 33, 0.67), rgba(99, 67, 50, 0.17));


  /*Font family*/
  --primary-font: 'Poppins', sans-serif;
}

html, body{
  font-size:14px;
  font-family: var(--primary-font);
  background-color:var(--bg-color);
}

/*Custome Scrollbar*/
::webkit-scrollbar{
  width:.375rem;
}

::webkit-scrollbar-track{
  background:var(--secondary-color);
}

::webkit-scrollbar-thumb{
  background:var(--primary-color);
}

/*Pre-defined*/
a{
  text-decoration: none;
}
.container-fluid{
  width:100%;
  margin-left:auto;
  margin-right:auto;
  padding-right:1rem;
  padding-left:1rem;
}
.container{
  width:auto;
  max-width:1320px;
  margin-left:autp;
  margin-right:auto;
  padding-right:1rem;
  padding-left:1rem;
}

.btn_wrapper{
  margin-top:5rem;
  @media(max-widt:992px){
    width:100%;
    text-align:center;
    display:block;
  }
  @media(max-width:767px){
    margin-bottom:3rem;
  }
  .btn{
    padding:0.857rem 2.571rem;
    font-size:1,143rem;
    border-radius:2.143rem;
    font-weight:600;
    box-shadow:0 0.5rem 1rem rgb(16,16,16);
    outline:2px solid var(--primary-color);
    outline-offset:0px;
    border:none;
    cursor:pointer;
    background:var(--primary-color);
    transition:all .3s ease-in-out;

    &:hover{
      outline-offset:10px;
    }
  }
}

h1{
  font-size:3.875rem;
  margin-bottom:1.25rem;
  font-weight:600;
  color:var(--white-color);
}

h2{
  font-size:2.875rem;
  font-weight:600;
  color:var(--white-color);
}

h3{
  font-size:2.25rem;
  font-weight:600;
  color:var(--white-color);
}

h4{
  font-size:1.5rem;
  line-height:2.25rem;
  font-weight:600;
  color:var(--white-color);
}

h5{
  font-size:1.5rem;
  line-height:1.2rem;
  margin-bottom:.9375rem;
  font-weight:600;
  color:var(--white-color);
}

h6{
  font-size:.875em;
  margin-bottom:.9375rem;
  text-transform:uppercase;
  font-weight:300;
  color:var(--white-color);
}

p{
  font-size:1rem;
  color:var(--font-color);
  line-height:1.625rem;
}